import React, { useEffect, useState } from "react";
import Footer from "../components/home/Footer";
import Header from "../components/home/Header";
import { Outlet, useLocation, useNavigate } from "react-router-dom";
import { Switch } from "antd";
import "../styles/Profile.scss";
import {
  AlertSVG,
  AppilatsiyaSVG,
  FileSVG,
  ResultSVG,
} from "../components/svg";
import {
  CheckUserConfig,
  GetAppHistorynConfig,
  GetApplicationConfig,
  GetDiplomConfig,
} from "src/server/config/Urls";
import { CatchError } from "src/utils";
import { useAppDispatch, useAppSelector } from "src/hooks";
import {
  setApplication,
  setHistory,
  setDiplom,
} from "src/redux/slices/application";
import { setApplied } from "src/redux/slices/application";

function Profile() {
  const navigate = useNavigate();
  const location = useLocation();
  const dispatch = useAppDispatch();
  const [section, setSection] = useState("");
  const tarix = useAppSelector((state) => state.Application.history);
  const diplom = useAppSelector((state) => state.Application.diplom);
  const userInfo = useAppSelector((state) => state.Application.user);
  const applied = useAppSelector((state) => state.Application.is_applied);
  const application = useAppSelector((state) => state.Application.application);

  const checkLocation = () => {
    if (location.pathname === "/profile" || location.pathname === "/profile/") {
      navigate("/profile/applications");
      setSection("applications");
    } else {
      const path = location.pathname.split("/profile/")[1];
      setSection(path);
    }
  };
  const changeLocation = (path: string) => {
    setSection(path);
    navigate(`/profile/${path}`);
  };
  const UserAccess = async () => {
    try {
      if (!applied) {
        const { data } = await CheckUserConfig();
        if (!data?.is_applied) {
          navigate("/status");
        }
        dispatch(setApplied(data?.is_applied));
        GetInformation(data?.is_applied);
      }
    } catch (err) {
      CatchError(err);
    }
  };
  const GetInformation = async (isApplied: boolean) => {
    if (isApplied) {
      try {
        // Set diplom info
        if (Object.keys(diplom).length < 1) {
          const diplom = await GetDiplomConfig();
          dispatch(setDiplom(diplom.data));
        }

        // Set Tarix info
        if (Object.keys(tarix).length < 1) {
          const tarix = await GetAppHistorynConfig();
          dispatch(setHistory(tarix.data));
        }

        // Set Application info
        if (Object.keys(application).length < 1) {
          const info = await GetApplicationConfig();
          dispatch(setApplication(info.data));
        }
      } catch (err) {
        CatchError(err);
      }
    }
  };

  useEffect(() => {
    checkLocation();
    UserAccess();
  }, []);
  return (
    <>
      <Header />
      <div className="profile">
        <div className="container">
          <div className="profile__sidebar">
            <div className="profile__header flex">
              <img
                src={
                  userInfo?.photo && `data:image/jpeg;base64,${userInfo?.photo}`
                }
                alt=""
              />
              <h4>
                {userInfo?.first_name} {userInfo?.last_name}
              </h4>
            </div>
            <div className="profile__menu">
              <h4>Mening arizalarim</h4>
              <button
                className={
                  section == "applications"
                    ? "profile__tab profile__tab-active flex"
                    : "profile__tab flex"
                }
                onClick={() => changeLocation("applications")}
              >
                <FileSVG />
                <span>Mening arizam (0)</span>
              </button>
              <button
                className={
                  section == "messages"
                    ? "profile__tab profile__tab-active flex"
                    : "profile__tab flex"
                }
                onClick={() => changeLocation("messages")}
              >
                <AlertSVG />
                <span>Xabarlar (0)</span>
              </button>
              <button
                className={
                  section == "results"
                    ? "profile__tab profile__tab-active flex"
                    : "profile__tab flex"
                }
                onClick={() => changeLocation("results")}
              >
                <ResultSVG />
                <span>Suhbat natijalari (0)</span>
              </button>
              <button
                className={
                  section == "appelatsiya"
                    ? "profile__tab profile__tab-active flex"
                    : "profile__tab flex"
                }
                onClick={() => changeLocation("appelatsiya")}
              >
                <AppilatsiyaSVG />
                <span>Apellyasiya berish</span>
              </button>
            </div>
            <div className="profile__notif">
              <h4>Sozlamalar</h4>
              <div className="flex">
                <span>Bildirishnomalar</span>
                <Switch defaultChecked />
              </div>
              <p>
                Telegram bot orqali obuna bolingan OTM larning yangi
                vakansiyalari va yuborgan arizalaringizni statuslaridan xabardor
                bolasiz.
              </p>
            </div>
            <div className="profile__links">
              <h4>Foydali xavolalar</h4>
              <a href="#">Telegram kanal (@eduuz)</a>
              <a href="#">Telegram bot (@elektron_talim_markazi)</a>
              <a href="#">Telegram bot (dghagdhgshgdhgsh)</a>
              <a href="#">Telegram bot (dghagdhgshgdhgsh)</a>
            </div>
          </div>
          <div className="profile__body">
            <Outlet />
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Profile;
