import { Alert } from "antd";
import React from "react";

function Result() {
  return (
    <div className="profile__result">
      <h2 className="title">Suhbat natijalari</h2>
      <Alert
        message="Diqqat"
        description="Hurmatli bitiruvchila arizangiz muvaffaqiyatli yuborildi. Arizani 2022-yilning 5-iyulidan 25-iyuliga qadar tahrirlash mumkin."
        type="success"
        showIcon
      />
    </div>
  );
}

export default Result;
