import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { GetDiplomConfig } from "src/server/config/Urls";
import { CatchError } from "src/utils";
import { RightArrowSVG } from "../../components/svg";

function Step2() {
  const navigate = useNavigate();
  const [diplom, setDiplom] = useState<any>({});

  const GetDiplom = async () => {
    try {
      const { data } = await GetDiplomConfig();
      setDiplom(data);
    } catch (error) {
      CatchError(error);
    }
  };

  useEffect(() => {
    GetDiplom();
  }, []);
  return (
    <>
      <div className="step2">
        {/* Title */}
        <h2 className="title">Diplom malumotlari</h2>

        {/* Body */}
        <div className="step2__body">
          <div className="flex">
            <h4>Texnikumnomi:</h4>
            <span>{diplom?.technical_college_name}</span>
          </div>
          <div className="flex">
            <h4>Yonalish nomi:</h4>
            <span>{diplom?.direction_name}</span>
          </div>
          <div className="flex">
            <h4>Mutaxassislik kasb nomi:</h4>
            <span>{diplom?.profession_name}</span>
          </div>
          <div className="flex">
            <h4>Diplom seriya raqami:</h4>
            <span>
              {diplom?.diploma_series} {diplom?.diploma_number}
            </span>
          </div>
          <div className="flex">
            <h4>Diplom berilgan sana:</h4>
            <span>{diplom?.diploma_given_year}</span>
          </div>
          <div className="flex">
            <h4>Talim tili:</h4>
            <span>{diplom?.education_language}</span>
          </div>
        </div>

        {/* Bottom button */}
        <div className="form">
          <div className="step__bottom">
            <button className="back" onClick={() => navigate("/register/1")}>
              <span>Orqaga</span>
            </button>
            <button
              className="arrowBtn"
              onClick={() => navigate("/register/3")}
            >
              <span>Keyingi qadam</span>
              <RightArrowSVG />
            </button>
          </div>
        </div>
      </div>
    </>
  );
}

export default Step2;
