import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { ILogin } from "src/types";

const initialState: ILogin = {
  all: {},
  phone: 0,
  user: {},
  isApplied: false,
};

export const orderSlice = createSlice({
  name: "login",
  initialState,
  reducers: {
    add: (state, action) => {
      state.all[action.payload.id] = action.payload;
    },
    set: (state, action) => {
      state.all = action.payload;
    },
    remove: (state, action: PayloadAction<string>) => {
      delete state.all[action.payload];
    },
    setPhone: (state, action) => {
      state.phone = action.payload;
    },
    setUser: (state, action) => {
      state.user = action.payload;
    },
    setApplied: (state, action) => {
      state.isApplied = action.payload;
    },
  },
});

// Action creators are generated for each case reducer function
export const { add, set, remove, setPhone, setUser, setApplied } =
  orderSlice.actions;

export default orderSlice.reducer;
