import React, { useEffect } from "react";
import { Progress, Image } from "antd";
import Footer from "src/components/home/Footer";
import Header from "src/components/home/Header";
import { useAppSelector } from "src/hooks";
import { subject } from "src/types";
import "src/styles/Profile.scss";
import { AcceptSVG, DoneSVG, UnfinishSVG } from "src/components/svg";
import {
  setApplication,
  setApplied,
  setDiplom,
  setUser,
} from "src/redux/slices/application";
import { CatchError, prettyDate } from "src/utils";
import { useAppDispatch } from "src/hooks";
import {
  CheckUserConfig,
  GetApplicationConfig,
  GetDiplomConfig,
  GetUserConfig,
} from "src/server/config/Urls";
import { useNavigate } from "react-router-dom";
import { token } from "src/server/Host";

function MyApplication() {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const diplom = useAppSelector((state) => state.Application.diplom);
  const userInfo = useAppSelector((state) => state.Application.user);
  const applied = useAppSelector((state) => state.Application.is_applied);
  const application = useAppSelector((state) => state.Application.application);

  // User grades
  const AllGrades = (grades: any) => {
    const FullSemesters: subject[] = [];
    grades.forEach((semestr: any) => {
      for (let item in semestr) {
        FullSemesters.push(semestr[item]);
      }
    });
    return FullSemesters;
  };

  // Get user informations
  const GetInformation = async () => {
    if (token) {
      const { data } = await CheckUserConfig();
      dispatch(setApplied(data?.is_applied));
      if (!data?.is_applied) {
        navigate("/status");
      } else {
        try {
          // Set diplom info
          if (Object.keys(userInfo).length < 1) {
            const me = await GetUserConfig();
            dispatch(setUser(me.data));
          }

          // Set diplom info
          if (Object.keys(diplom).length < 1) {
            const diplom = await GetDiplomConfig();
            dispatch(setDiplom(diplom.data));
          }

          // Set Application info
          if (Object.keys(application).length < 1) {
            const info = await GetApplicationConfig();
            dispatch(setApplication(info.data));
          }
        } catch (err) {
          CatchError(err);
        }
      }
    } else {
      navigate("/");
    }
  };

  useEffect(() => {
    GetInformation();
  }, []);
  return (
    <div className="my-application">
      <Header />

      <div className="container">
        <h2 className="title">Mening arizam</h2>
        <div className="my-application__body">
          <div className="my-application__main">
            {/* Malumot */}
            <div className="malumot">
              <div className="id flex">
                <h4>Ariza ID: {application?.id}</h4>
                <span>
                  Ariza topshiorilgan vaqt:{" "}
                  <b>{prettyDate(application?.created_at)}</b>
                </span>
              </div>
              <div className="info">
                <Image
                  width={180}
                  src={
                    userInfo?.photo &&
                    `data:image/jpeg;base64,${userInfo?.photo}`
                  }
                />
                <div className="info__section">
                  <h2 className="title">
                    {userInfo?.first_name} {userInfo?.last_name}
                  </h2>
                  <div>
                    <div className="flex">
                      <h4>Jinsi va tugilgan yili:</h4>
                      <h5>
                        {new Date(userInfo?.birth_date).toLocaleDateString()}
                      </h5>
                    </div>
                    <div className="flex">
                      <h4>Fuqaroligi:</h4>
                      <h5>{userInfo?.citizenship?.name}</h5>
                    </div>
                    <div className="flex">
                      <h4>Doimiy yashash manzili:</h4>
                      <h5>{userInfo?.permanent_address}</h5>
                    </div>
                    <div className="flex">
                      <h4>J.SH.SH.I.R:</h4>
                      <h5>{userInfo?.pinfl}</h5>
                    </div>
                    <div className="flex">
                      <h4>Telefon raqam:</h4>
                      <h5>+{userInfo?.phone_number}</h5>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {/* ARIZA */}
            <section className="ariza">
              <h2 className="title">Ariza malumotlari</h2>
              <div className="section__body">
                <div className="flex">
                  <h4>OTM nomi:</h4>
                  <span>{application?.university_name}</span>
                </div>
                <div className="flex">
                  <h4>Yonalish nomi:</h4>
                  <span>{application?.university_direction_name}</span>
                </div>

                <div className="flex">
                  <h4>Talim tili:</h4>
                  <span>{application?.education_language_name}</span>
                </div>
                <div className="flex">
                  <h4>Ta'lim turi:</h4>
                  <span>{application?.education_type_name}</span>
                </div>
              </div>
            </section>

            {/* DIPLOM */}
            <section className="diplom">
              <h2 className="title">Diplom malumotlari</h2>
              <div className="section__body">
                <div className="flex">
                  <h4>Texnikumnomi:</h4>
                  <span>{diplom?.technical_college_name}</span>
                </div>
                <div className="flex">
                  <h4>Yonalish nomi:</h4>
                  <span>{diplom?.profession_name}</span>
                </div>
                <div className="flex">
                  <h4>Mutaxassislik kasb nomi:</h4>
                  <span>{diplom?.direction_name}</span>
                </div>
                <div className="flex">
                  <h4>Diplom seriya raqami:</h4>
                  <span>
                    {diplom?.diploma_series} {diplom?.diploma_number}
                  </span>
                </div>
                <div className="flex">
                  <h4>Diplom berilgan sana:</h4>
                  <span>{diplom?.diploma_given_year}</span>
                </div>
                <div className="flex">
                  <h4>Talim tili:</h4>
                  <span>{diplom?.education_language}</span>
                </div>
              </div>
            </section>

            {/* BAHOLARI */}
            <section className="baholar">
              <h2 className="title">Diplom malumotlari</h2>
              {/* Alert */}
              <div className="baholar__alert-success">
                <h4>Diqqat!</h4>
                <p>
                  Hurmatli bituruvchi ariza topshira olishingiz uchun{" "}
                  <span>
                    baholarining kamida 50 foizi (tibbiyot oliy talim
                    muassasalari uchun kamida 85 foizi) 5 (alo) bolishi
                    kerak.
                  </span>
                </p>
                <div className="bottom flex">
                  <p>
                    Szining <span>5 ( alo)</span> baholaringiz foiz kesimida{" "}
                  </p>
                  <Progress percent={diplom?.percent_of_five} status="active" />
                </div>
              </div>

              {/* Semestrs */}
              <div>
                {diplom?.grades &&
                  AllGrades(diplom?.grades).map(
                    (semestr: any, index: number) => (
                      <div className="baholar__semestr" key={index}>
                        <div className="baholar__semestr-name">
                          {index + 1} - semestr
                        </div>
                        <div>
                          {semestr.map((sub: subject) => (
                            <div
                              key={sub.subject}
                              className="baholar__semestr-row flex"
                            >
                              <h3>{sub.subject}</h3>
                              <span>
                                {sub.ball}{" "}
                                {sub.ball == 3
                                  ? "(qoniqarli)"
                                  : sub.ball == 4
                                  ? "(yaxshi)"
                                  : "(a'lo)"}
                              </span>
                            </div>
                          ))}
                        </div>
                      </div>
                    )
                  )}
              </div>
            </section>
          </div>
          <div className="my-application__sidebar">
            <h2>Ariza holati</h2>
            <div>
              <div className="status">
                <DoneSVG /> <span>Ariza yuborildi</span>
              </div>
              <div className="status">
                <AcceptSVG /> <span>Ariza qabul qilindi</span>
              </div>
              <div className="status">
                <UnfinishSVG />
                <div>
                  <span>Suhbat sanasi aniqlandi</span>
                  <h5>Otish</h5>
                </div>
              </div>
              <div className="status">
                <UnfinishSVG />
                <div>
                  <span>Natijangiz elon qilindi</span>
                  <h5>Otish</h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
}

export default MyApplication;
