import React from "react";
import { useAppSelector } from "src/hooks";
import Application from "src/components/profile/application";

function Applications() {
  const history = useAppSelector((state) => state.Application.history);
  const application = useAppSelector((state) => state.Application.application);
  return (
    <div className="profile__application">
      <h2>Mening arizam</h2>
      <div className="applications">
        <Application data={application} footer={true} />
      </div>
      <div className="profile__application__info">
        <h4>Diqqat!</h4>
        <p>
          Hurmatli bitiruvchila arizalar 2022-yilning 5-iyulidan 25-iyuliga
          qadar qabul qilinadi va tahrirlash ham 25-iyulga qadar faol turadi.
        </p>
      </div>
      <div className="profile__application__history">
        <h2>Mening arizam tarixi</h2>
        <div className="histories">
          {history.length
            ? history?.map((item: any, index: number) => (
                <Application data={item} footer={false} key={index} />
              ))
            : ""}
        </div>
      </div>
    </div>
  );
}

export default Applications;
