import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import {
  Form,
  Alert,
  Input,
  Modal,
  Button,
  Divider,
  message,
  Dropdown,
} from "antd";
import {
  ChangePasswordConfig,
  CheckPhoneConfig,
  GetUserConfig,
  LoginConfig,
  RegisterCheckConfig,
  RegisterConfig,
  ResendConfig,
} from "src/server/config/Urls";
import { setPhone } from "src/redux/slices/login";
import { useAppDispatch, useAppSelector } from "src/hooks";
import { setCookie, deleteCookie } from "src/server/Cookies";
import { ACCESS, REFRESH, ROLE, token } from "src/server/Host";
import { phone, passport_date, jshshr } from "src/assets/rules";
import {
  AlertSVG,
  AppilatsiyaSVG,
  BottomArrowSVG,
  FileSVG,
  LeftArrowSVG,
  ResultSVG,
} from "../svg";
import { CatchError } from "src/utils";
import Timer from "../Counter";
import { setUser } from "src/redux/slices/application";

function Header() {
  // Accesing store
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const loginStore = useAppSelector((state) => state.Login);
  const userInfo = useAppSelector((state) => state.Application.user);

  // Modal visibility
  const [login, setLogin] = useState(false);
  const [aktiveTimer, setAktiveTimer] = useState(false);
  const [isModalVisible, setIsModalVisible] = useState(false);

  // Modal configuratuion
  const [modal, setModal] = useState("oneId");
  const [title, setTitle] = useState("Kirish yoki royxatdan otish");

  // Forms
  const [loginForm] = Form.useForm();
  const [registerForm] = Form.useForm();
  const [GetSMS__ResetForm] = Form.useForm();
  const [CheckSMS__ResetForm] = Form.useForm();
  const [CheckSMS__RegistertForm] = Form.useForm();

  // Login with phone and password
  const onLogin = async (values: any) => {
    const newData = {
      phone_number: `998${values.phone_number}`,
      password: values.password,
    };
    try {
      const { data } = await LoginConfig(newData);

      // Set cookies
      setCookie(ACCESS, data.access);
      setCookie(REFRESH, data.refresh);
      setIsModalVisible(false);
      checkUser();
      window.location.href = "/profile/applications";
    } catch (err) {
      CatchError(err);
    }
  };

  // Check phone Number
  const checkPhone = async ({ phone }: { phone: number }) => {
    dispatch(setPhone(phone));
    try {
      await CheckPhoneConfig(`998${phone}`);
      setTitle("Kirish");
      setModal("login");
      loginForm.setFieldsValue({
        phone_number: phone,
      });
    } catch (err) {
      CatchError(err);
      setTitle("Ro'yhatdan o'tish");
      setModal("register");
      registerForm.setFieldsValue({
        phone_number: phone,
      });
    }
  };

  // Get SmS for reset
  const getSMS__reset = async (values: any) => {
    try {
      await ResendConfig({ phone_number: `998${values.phone_number}` });
      setModal("check__reset");
      setTitle("SMS ni tasdiqlash");

      // Countdown for re send button
      setAktiveTimer(true);
    } catch (err) {
      CatchError(err);
    }
  };

  // Re send sms
  const handleClick = async () => {
    try {
      await ResendConfig({ phone_number: `998${loginStore.phone}` });
    } catch (err) {
      CatchError(err);
    }
  };

  // Check SMS by reset
  const check__reset = async (values: any) => {
    try {
      await ChangePasswordConfig(`998${loginStore.phone}`, values);
      setTitle("Kirish");
      setModal("login");
      loginForm.setFieldsValue({
        phone_number: loginStore.phone,
      });
    } catch (err) {
      CatchError(err);
    }
  };

  // Check SMS by Register -----------------------------------
  const check__register = async (values: any) => {
    const dataForm = {
      phone_number: `998${loginStore.phone}`,
      ...values,
    };
    try {
      const { data } = await RegisterCheckConfig(dataForm);

      if (data.role === "applicant") {
        // Set cookies
        setCookie(ROLE, data.role);
        setCookie(ACCESS, data.access);
        setCookie(REFRESH, data.refresh);
        setIsModalVisible(false);
        checkUser();
        window.location.href = "/profile";
      } else {
        message.error("Sizda kirish uchun huquq yo'q");
      }
    } catch (err) {
      CatchError(err);
    }
  };

  // Register form
  const register_form = async (values: any) => {
    const newData = {
      phone_number: `998${values.phone_number}`,
      pinfl: values.pinfl,
      pport_given_date: values.pport_given_date,
    };
    try {
      const { data } = await RegisterConfig(newData);
      console.log("data :>> ", data);
      setModal("check__register");
      setTitle("SMS ni tasdiqlash");
    } catch (err) {
      CatchError(err);
      navigate("/status");
    }
  };

  // Check user from local storage
  const checkUser = async () => {
    token && setLogin(true);
  };

  // Logout function
  const logout = () => {
    // Delete cookies
    deleteCookie(ROLE);
    deleteCookie(ACCESS);
    deleteCookie(REFRESH);
    window.location.href = "/";
    checkUser();
  };

  // Profile menu
  const menu = (
    <div className="kirgan__menu">
      <button
        className="profile__tab flex"
        onClick={() => navigate("/profile/applications")}
      >
        <FileSVG />
        <span>Mening arizam (0)</span>
      </button>
      <button
        className="profile__tab flex"
        onClick={() => navigate("/profile/messages")}
      >
        <AlertSVG />
        <span>Xabarlar (0)</span>
      </button>
      <button
        className="profile__tab flex"
        onClick={() => navigate("/profile/results")}
      >
        <ResultSVG />
        <span>Suhbat natijalari (0)</span>
      </button>
      <button
        className="profile__tab flex"
        onClick={() => navigate("/profile/appelatsiya")}
      >
        <AppilatsiyaSVG />
        <span>Apellyasiya berish</span>
      </button>
      <button className="logout" onClick={() => logout()}>
        Chiqish
      </button>
    </div>
  );

  useEffect(() => {
    checkUser();
  }, []);

  return (
    // Header section
    <div className="header">
      <div className="container">
        {/* Logo side */}
        <Link to={"/"} className="flex logo">
          <img src={require("src/assets/images/logo.png")} />
          <span>Suhbat.edu.uz</span>
        </Link>

        {/* Navbar */}
        <div className="header__nav">
          <Link to="#Asosiy">Asosiy</Link>
          <Link to="#Yangiliklar">Yangiliklar</Link>
          <Link to="#Statistika">Statistika</Link>

          {/* Profile section */}
          <div
            className="header__profile"
            onClick={() => {
              setModal("oneId");
              setTitle("Kirish yoki royxatdan otish");
            }}
          >
            {!login ? (
              <button
                className="kirish"
                onClick={() => setIsModalVisible(true)}
              >
                Kirish
              </button>
            ) : (
              <Dropdown
                overlay={menu}
                trigger={["click"]}
                placement="bottomRight"
              >
                <div className="kirgan">
                  <div className="kirgan__img">
                    {userInfo?.first_name
                      ? `${userInfo?.first_name.slice(
                          0,
                          1
                        )}${userInfo?.last_name.slice(0, 1)}`
                      : "AA"}
                  </div>
                  <span>Mening profilim</span>
                  <BottomArrowSVG />
                </div>
              </Dropdown>
            )}
          </div>
        </div>

        {/* Login Modal */}
        <Modal
          className="login__modal"
          title={title}
          visible={isModalVisible}
          onCancel={() => setIsModalVisible(false)}
          footer={null}
        >
          {/* Modal 1 kirish */}
          {modal === "oneId" && (
            <div className="modal1">
              <Form
                name="basic"
                onFinish={checkPhone}
                autoComplete="off"
                layout="vertical"
              >
                <Form.Item
                  label="Telifon raqamingiz"
                  name="phone"
                  rules={phone}
                >
                  <Input addonBefore="+998" placeholder="XX XXX XX XX" />
                </Form.Item>

                <Form.Item>
                  <Button type="primary" htmlType="submit">
                    Kirish
                  </Button>
                </Form.Item>
              </Form>

              <Divider>yoki</Divider>

              <button
                onClick={() => {
                  window.location.href =
                    "https://suhbat.edu.uz/api/auth/one-id/";
                }}
              >
                ONE ID orqali
              </button>
            </div>
          )}

          {/* Sign in with phone and password */}
          {modal === "login" && (
            <div className="modal2">
              <Form
                name="basic"
                onFinish={onLogin}
                autoComplete="off"
                layout="vertical"
                form={loginForm}
              >
                <Form.Item
                  label="Telifon raqamingiz"
                  name="phone_number"
                  rules={phone}
                >
                  <Input
                    addonBefore="+998"
                    placeholder="XX XXX XX XX"
                    disabled
                  />
                </Form.Item>

                <Form.Item
                  label="Parol"
                  name="password"
                  rules={[{ required: true, message: "Parolni kiriting!" }]}
                >
                  <Input.Password placeholder="Parol" />
                </Form.Item>

                <p
                  onClick={() => {
                    setModal("getSMS__reset");
                    setTitle("Parolni qayta tiklang !");
                    GetSMS__ResetForm.setFieldsValue({
                      phone_number: loginStore.phone,
                    });
                  }}
                >
                  Parolni unutdingizmi?
                </p>

                <Form.Item>
                  <Button type="primary" htmlType="submit">
                    Kirish
                  </Button>
                </Form.Item>
              </Form>
              <button
                className="backButton"
                onClick={() => {
                  setModal("oneId");
                  setTitle("Kirish yoki royxatdan otish");
                }}
              >
                <LeftArrowSVG />
              </button>
            </div>
          )}

          {/* Get Sms code  */}
          {modal === "getSMS__reset" && (
            <div className="modal3">
              <Form
                name="basic"
                onFinish={getSMS__reset}
                autoComplete="off"
                layout="vertical"
                form={GetSMS__ResetForm}
              >
                <Alert
                  message="Telefon raqamingizni yozing va biz sizga yangi parolni yuboramiz. "
                  type="info"
                  className="alert"
                />
                <Form.Item
                  label="Telifon raqamingiz"
                  name="phone_number"
                  rules={phone}
                >
                  <Input
                    addonBefore="+998"
                    placeholder="XX XXX XX XX"
                    disabled
                  />
                </Form.Item>

                <Form.Item>
                  <Button type="primary" htmlType="submit">
                    Ozgartirish
                  </Button>
                </Form.Item>
              </Form>
              <button
                className="backButton"
                onClick={() => {
                  setTitle("Kirish");
                  setModal("login");
                }}
              >
                <LeftArrowSVG />
              </button>
            </div>
          )}

          {/* Check sms qayta tiklash */}
          {modal === "check__reset" && (
            <div className="modal4">
              <Form
                name="basic"
                onFinish={check__reset}
                autoComplete="off"
                layout="vertical"
                form={CheckSMS__ResetForm}
              >
                <Alert
                  message="Sizning raqamingizga parol sms orqali yuborildi."
                  type="info"
                  className="alert"
                />
                <Form.Item
                  label="Parol"
                  name="password"
                  rules={[{ required: true, message: "Parolni kiriting!" }]}
                >
                  <Input.Password placeholder="Parol" />
                </Form.Item>

                <Form.Item>
                  <Button type="primary" htmlType="submit">
                    Tasdiqlash
                  </Button>
                </Form.Item>
              </Form>
              <Timer
                aktiveTimer={aktiveTimer}
                setAktiveTimer={setAktiveTimer}
                handleClick={handleClick}
              />
              <button
                className="backButton"
                onClick={() => {
                  setModal("getSMS__reset");
                  setTitle("Parolni qayta tiklang !");
                  GetSMS__ResetForm.setFieldsValue({
                    phone_number: loginStore.phone,
                  });
                }}
              >
                <LeftArrowSVG />
              </button>
            </div>
          )}

          {/* Check sms code from register */}
          {modal === "check__register" && (
            <div className="modal4">
              <Form
                name="basic"
                onFinish={check__register}
                autoComplete="off"
                layout="vertical"
                form={CheckSMS__RegistertForm}
              >
                <Alert
                  message="Sizning raqamingizga parol sms orqali yuborildi."
                  type="info"
                  className="alert"
                />
                <Form.Item
                  label="Parol"
                  name="code"
                  rules={[{ required: true, message: "Parolni kiriting!" }]}
                >
                  <Input.Password placeholder="Parol" />
                </Form.Item>

                <Form.Item>
                  <Button type="primary" htmlType="submit">
                    Tasdiqlash
                  </Button>
                </Form.Item>
              </Form>
              <Timer
                aktiveTimer={aktiveTimer}
                setAktiveTimer={setAktiveTimer}
                handleClick={handleClick}
              />
              <button
                className="backButton"
                onClick={() => {
                  setTitle("Ro'yhatdan o'tish");
                  setModal("register");
                }}
              >
                <LeftArrowSVG />
              </button>
            </div>
          )}

          {/* Register */}
          {modal === "register" && (
            <div className="modal5">
              <Form
                name="basic"
                onFinish={register_form}
                autoComplete="off"
                form={registerForm}
                layout="vertical"
              >
                <Form.Item
                  label="Telifon raqamingiz"
                  name="phone_number"
                  rules={phone}
                >
                  <Input addonBefore="+998" placeholder="XX XXX XX XX" />
                </Form.Item>
                <Form.Item
                  label="Pasport berilgan sana"
                  name="pport_given_date"
                  rules={passport_date}
                >
                  <Input placeholder="YYYY-MM-DD" />
                </Form.Item>
                <Form.Item label="JSHSHIR" name="pinfl" rules={jshshr}>
                  <Input placeholder="1234567891011121314" />
                </Form.Item>
                <Alert
                  message="'SMS-xabar' xizmati arizalarni ko'rib chiqish jarayonida arizani tasdqilash uchun mo'ljallangan. 'SMS-xabar' xizmati uchun 40 so'm to'lov undiriladi. SMS xabar abonentning hisobida yetarli miqdorda mablag' bo'lsagina boradi."
                  type="info"
                  className="alert"
                />

                <Form.Item>
                  <Button type="primary" htmlType="submit">
                    Tasdiqlash
                  </Button>
                </Form.Item>
              </Form>
              <button
                className="backButton"
                onClick={() => {
                  setModal("oneId");
                  setTitle("Kirish yoki royxatdan otish");
                }}
              >
                <LeftArrowSVG />
              </button>
            </div>
          )}
        </Modal>
      </div>
    </div>
  );
}

export default Header;
