import React, { useEffect } from "react";
import Footer from "../components/home/Footer";
import { optionAlert, optionDone } from "../assets/lottie";
import { CheckUserConfig, GetUserConfig } from "../server/config/Urls";
import { useNavigate } from "react-router-dom";
import { token } from "../server/Host";
import Lottie from "react-lottie";
import "../styles/Status.scss";
import { useAppDispatch, useAppSelector } from "../hooks";
import { CatchError } from "../utils";
import { setUser } from "src/redux/slices/application";
import Header from "src/components/home/Header";

function Status() {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const user = useAppSelector((state) => state.Application.user);

  // Get user data
  const GetUser = async () => {
    if (token) {
      try {
        const { data } = await GetUserConfig();
        dispatch(setUser(data));
      } catch (err) {
        CatchError(err);
      }
    }
  };

  // Check user applied or not
  const UserAccess = async () => {
    if (token) {
      try {
        const { data } = await CheckUserConfig();
        if (data?.is_applied) {
          navigate("/profile/applications");
        } else {
          GetUser();
        }
      } catch (err) {
        CatchError(err);
      }
    }
  };

  useEffect(() => {
    UserAccess();
  }, []);

  return (
    <div className="status">
      <Header />

      {/* Body */}
      <div className="status__body ">
        <div className="container">
          {/* Status 1 */}
          {token && (
            <div className="status__box status1">
              <div className="status__lottie">
                <Lottie options={optionDone} height={180} width={180} />
              </div>
              <h2 className="status__title">
                {user.first_name} {user.last_name}
              </h2>
              <div className="status__info">
                Suhbat.edu.uz tizimiga xush kelibsiz!
              </div>
              <button
                className="status__button"
                onClick={() => navigate("/register/1")}
              >
                Ariza topshirish
              </button>
              <p className="status__label">
                Ozbekiston Respublikasi{" "}
                <a href="https://lex.uz/uz/" target="_blank" rel="noreferrer">
                  {" "}
                  qonunchiligi{" "}
                </a>
                asosida, shaxsingiz togrisidagi malumotlarni
                <a
                  href="https://id.egov.uz/ru"
                  target="_blank"
                  rel="noreferrer"
                >
                  {" "}
                  id.egov.uz{" "}
                </a>{" "}
                tizimidan olinganini malum qilamiz
              </p>
            </div>
          )}

          {/* Status 2 */}
          {!token && (
            <div className="status__box status2">
              <div className="status__lottie">
                <Lottie options={optionAlert} height={180} width={180} />
              </div>
              <h2 className="status__title">
                Kechirasiz sizning malumotlaringiz texnikum bitiruvchilari
                royhatidan topilamdi
              </h2>

              <button className="status__button" onClick={() => navigate("/")}>
                Bosh saxifaga qaytish
              </button>
              <p className="status__label">
                Ozbekiston Respublikasi{" "}
                <a href="https://lex.uz/uz/" target="_blank" rel="noreferrer">
                  {" "}
                  qonunchiligi{" "}
                </a>
                asosida, shaxsingiz togrisidagi malumotlarni
                <a
                  href="https://id.egov.uz/ru"
                  target="_blank"
                  rel="noreferrer"
                >
                  {" "}
                  id.egov.uz{" "}
                </a>{" "}
                tizimidan olinganini malum qilamiz
              </p>
            </div>
          )}
        </div>
      </div>

      {/* Footer */}
      <Footer />
    </div>
  );
}

export default Status;
