import { message } from "antd";
import { deleteCookie } from "../server/Cookies";
import { ACCESS, REFRESH, ROLE } from "../server/Host";

export const CatchError = (error: any) => {
  if (error.response) {
    let obj = error.response.data;
    if (error.response.status === 403) {
      deleteCookie(ACCESS);
      deleteCookie(REFRESH);
      deleteCookie(ROLE);
      window.location.href = "/";

      // Displaying error messages
      for (let key in obj) {
        if (key !== "status") {
          if (obj[key] === "Not found.") {
            message.warning(
              `Sizning raqamingiz bazada yo'q. Iltimos ro'yhatdan o'ting !`
            );
          } else {
            if (key === "error") {
              message.error(obj.error.detail);
            }
            if (obj[key].length > 0 && typeof obj[key] === "string") {
              message.error(obj[key]);
            }
            if (Array.isArray(obj[key])) {
              obj[key]?.forEach((item: any) => {
                item.length > 0 && message.error(item);
              });
            }
          }
        }
      }
    } else {
      for (let key in obj) {
        if (key !== "status") {
          if (obj[key] === "Not found.") {
            message.warning(
              `Sizning raqamingiz bazada yo'q. Iltimos ro'yhatdan o'ting !`
            );
          } else {
            if (key === "error") {
              message.error(obj.error.detail);
            }
            if (obj[key].length > 0 && typeof obj[key] === "string") {
              message.error(obj[key]);
            }
            if (Array.isArray(obj[key])) {
              obj[key]?.forEach((item: any) => {
                item.length > 0 && message.error(item);
              });
            }
          }
        }
      }
    }
  }
};
// Pretty date
export const prettyDate = (time = "29-10-2022 15:20:38") => {
  const date = new Date(time);
  const monthes: { [id: number]: string } = {
    1: "Yanvar",
    2: "Fevral",
    3: "Mart",
    4: "Aprel",
    5: "May",
    6: "Iyun",
    7: "Iyul",
    8: "Avgust",
    9: "Sentyabr",
    10: "Oktyabr",
    11: "Noyabr",
    12: "Dekabr",
  };
  return `${date.getHours()}:${
    date.getMinutes() < 10 ? "0" : ""
  }${date.getMinutes()}, ${date.getDate()} ${
    monthes[date.getMonth()]
  }, ${date.getFullYear()}`;
};
