import { Button, Form } from "antd";
import { useEffect, useState } from "react";
import TextArea from "antd/lib/input/TextArea";
import { appelatsiya } from "../../assets/rules";

function Appelatsiya() {
  const [form] = Form.useForm();
  const [, forceUpdate] = useState({});
  const [message, setMessage] = useState("");

  // To disable submit button at the beginning.
  useEffect(() => {
    forceUpdate({});
  }, []);

  const onFinish = ({ text }: { text: string }) => {
    setMessage(text);
  };

  return (
    <div className="profile__appelatsiya">
      <h2 className="title">Apellyasiya berish</h2>
      <div className="appelatsiya">
        <div className="appelatsiya__info">
          <h4>Diqqat!</h4>
          <p>
            Apellyasiya shikoyatlari suhbat natijalari elon qilingan vaqtdan
            boshlab 24 soat ichida oliy talim muassasasida togridan-togri
            yozma shaklda yoki Oliy va orta maxsus talim vazirligining rasmiy
            veb-sayti orqali onlayn shaklda qabul qilinadi va appelyasiya
            komissiyasi tomonidan murojaat qilganlarning sonidan kelib chiqqan
            holda bir kundan uch kungacha bolgan muddatda videoyozuv asosida
            korib chiqiladi.
          </p>
        </div>
        {/* {message ? (
          <div className="appelatsiya__done">
            <div className="appelatsiya__header flex">
              <h2>Apellyasiya ID: 123456</h2>
              <span>
                Apellyasiya topshiorilgan vaqt:{" "}
                <b>{new Date().toLocaleString()}</b>
              </span>
            </div>
            <p>{message}</p>
            <div className="success">
              <h4>Ariza yuborildi!</h4>
              <p>
                Mutaxasislar arizani organib chiqib tez orada sizga xabar
                yuborishadi.
              </p>
            </div>
          </div>
        ) : (
          <div className="appelatsiya__body">
            <h2>Apellyasiya berish</h2>
            <Form
              layout="vertical"
              name="appelatsiya"
              onFinish={onFinish}
              form={form}
            >
              <Form.Item
                name="text"
                label="Apellyasiya berish sababini yozma shaklda kiriting"
                rules={appelatsiya}
              >
                <TextArea rows={6} />
              </Form.Item>
              <Form.Item shouldUpdate>
                {() => (
                  <Button
                    type="primary"
                    htmlType="submit"
                    disabled={
                      !form.isFieldsTouched(true) ||
                      !!form
                        .getFieldsError()
                        .filter(({ errors }) => errors.length).length
                    }
                  >
                    Yuborish
                  </Button>
                )}
              </Form.Item>
            </Form>
          </div>
        )} */}
      </div>
    </div>
  );
}

export default Appelatsiya;
