import React from "react";
import { Link } from "react-router-dom";
import {
  FacebookSVG,
  InstagramSvg,
  MailSVG,
  PhoneSVG,
  TelegramSVG,
} from "../svg";

function Footer() {
  return (
    <div className="footer">
      <div className="container">
        <div className="footer__nav">
          <div className="footer__info">
            <Link to={"/"} className="logo">
              <img src={require("../../assets/images/logo.png")} alt="" />
              <span>Suhbat.edu.uz</span>
            </Link>
            <p className="info">
              Portalda elon qilingan materiallardan nusxa kochirish, tarqatish
              va boshqa shakllarda foydalanish faqat tahririyat yozma roziligi
              bilan amalga oshirilishi mumkin.
            </p>
          </div>
          <div className="footer__links">
            <h4>Asosiy</h4>
            <span>Imtiyozlar</span>
            <span>Yangilklar</span>
            <span>Satastitika</span>
          </div>
          <div className="footer__socials">
            <h4>Biz ijtimoiy tarmoqlarda</h4>
            <a href="" className="social">
              <InstagramSvg />
              <span> Instagram</span>
            </a>
            <a href="" className="social">
              <TelegramSVG />
              <span> Telegram</span>
            </a>
            <a href="" className="social">
              <FacebookSVG />
              <span> Facebook</span>
            </a>
          </div>
          <div className="footer__contact">
            <h4>Biz ijtimoiy tarmoqlarda</h4>
            <a href="" className="contact">
              <PhoneSVG />
              <span> +998336959550</span>
            </a>
            <a href="" className="contact">
              <PhoneSVG />
              <span> +998336959550</span>
            </a>
            <a href="" className="contact">
              <MailSVG />
              <span> aviapack@gmail.com</span>
            </a>
          </div>
        </div>
        <p>2021  Vakansiya edu.uz</p>
      </div>
    </div>
  );
}

export default Footer;
