import React, { useRef, useState, useEffect } from "react";
import { Button, message } from "antd";

function Timer({
  aktiveTimer,
  setAktiveTimer,
  handleClick,
}: {
  aktiveTimer: Boolean;
  setAktiveTimer: any;
  handleClick: any;
}) {
  const [counter, setCounter] = useState(120);
  const ref = useRef(120);
  const makeTime = () => {
    if (aktiveTimer) {
      if (ref.current > 0) {
        ref.current = ref.current - 1;
        setTimeout(() => {
          setCounter(ref.current);
          makeTime();
        }, 1000);
      } else if (ref.current === 0) {
        message.warning("Sms vaqti tugadi");
        setAktiveTimer(false);
      }
    }
  };
  const reSendCode = () => {
    ref.current = 120;
    setAktiveTimer(true);
    handleClick();
  };

  useEffect(() => {
    makeTime();
  }, [aktiveTimer]);

  return (
    <button
      className={counter == 0 ? "reSend reSend-active" : "reSend"}
      disabled={counter !== 0}
      onClick={reSendCode}
    >
      <span>Kodni qayta yuborish </span> 0
      {counter === 120 ? "2" : counter >= 60 ? "1" : "0"}:
      {counter === 120 || counter === 60
        ? "00"
        : counter > 60
        ? `${counter < 70 && counter > 59 ? "0" : ""}${counter - 60}`
        : `${counter < 10 ? "0" : ""}${counter}`}
    </button>
  );
}

export default Timer;
