import { Progress } from "antd";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { GetDiplomConfig } from "src/server/config/Urls";
import { CatchError } from "src/utils";
import { RightArrowSVG } from "../../components/svg";
import { subject } from "src/types";

function Step3() {
  const navigate = useNavigate();
  const [hasThree, setHasThree] = useState<boolean>(true);
  const [grades, setGrades] = useState<any>([]);
  const [badGrades, setBadGrades] = useState<any>([]);
  const [percent, setPercent] = useState<number>(0);

  const GetDiplom = async () => {
    try {
      const { data } = await GetDiplomConfig();
      setHasThree(data?.is_have_three);
      setPercent(data?.percent_of_five);
      AllGrades(data?.grades);
    } catch (error) {
      CatchError(error);
    }
  };
  const AllGrades = (grades: any) => {
    const FullSemesters: subject[] = [];
    const BadSemesters: subject[] = [];
    grades.forEach((semestr: any) => {
      for (let item in semestr) {
        FullSemesters.push(semestr[item]);

        // Get all 3 baholilar
        semestr[item].map((sub: subject) => {
          if (sub.ball == 3) {
            BadSemesters.push(sub);
          }
        });
      }
    });
    setGrades(FullSemesters);
    setBadGrades(BadSemesters);
  };

  useEffect(() => {
    GetDiplom();
  }, []);
  return (
    <>
      <div className="step3">
        {/* Title */}
        <h2 className="title">Semmestr baxolari</h2>

        {/* Body */}
        <div className="step3__body">
          {/* Alert */}
          <div>
            {hasThree && (
              <div className="step3__alert-error">
                <h4>Diqqat!</h4>
                <p>
                  Hurmatli bituruvchi sizning{" "}
                  {badGrades.map((sub: subject) => (
                    <b key={sub.subject}>
                      {`${String(sub.semester)}-semestr ${sub.subject} fanidan 3
                        bahongiz, `}
                    </b>
                  ))}
                  bolgani sababli ariza topshira ololmaysiz.
                </p>
              </div>
            )}

            {!hasThree && (
              <div className="step3__alert-success">
                <h4>Diqqat!</h4>
                <p>
                  Hurmatli bituruvchi ariza topshira olishingiz uchun{" "}
                  <span>
                    baholarining kamida 50 foizi (tibbiyot oliy talim
                    muassasalari uchun kamida 85 foizi) 5 (alo) bolishi
                    kerak.
                  </span>
                </p>
                <div className="bottom flex">
                  <p>
                    Szining <span>5 ( alo)</span> baholaringiz foiz kesimida{" "}
                  </p>
                  <Progress percent={percent} status="active" />
                </div>
              </div>
            )}
          </div>

          {/* Semestrs */}
          <div>
            {grades.map((semestr: subject[], index: number) => (
              <div className="step3__semestr" key={index}>
                <div className="step3__semestr-name">{index + 1} - semestr</div>
                <div>
                  {semestr.map((sub: subject) => (
                    <div
                      key={sub.subject}
                      className={
                        sub.ball == 3
                          ? "step3__semestr-row error flex"
                          : "step3__semestr-row flex"
                      }
                    >
                      <h3>{sub.subject}</h3>
                      <span>
                        {sub.ball}{" "}
                        {sub.ball == 3
                          ? "(qoniqarli)"
                          : sub.ball == 4
                          ? "(yaxshi)"
                          : "(a'lo)"}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Bottom button */}
        <div className="form">
          <div className="step__bottom">
            <button className="back" onClick={() => navigate("/register/2")}>
              <span>Orqaga</span>
            </button>
            <button
              className="arrowBtn"
              disabled={hasThree || percent < 50}
              onClick={() => navigate("/register/4")}
            >
              <span>Keyingi qadam</span>
              <RightArrowSVG />
            </button>
          </div>
        </div>
      </div>
    </>
  );
}

export default Step3;
