import React, { useEffect } from "react";
import { Outlet, useLocation, useNavigate } from "react-router-dom";
import Footer from "src/components/home/Footer";
import {
  step1,
  step1done,
  step2,
  step2current,
  step2done,
  step3,
  step3current,
  step3done,
  success,
} from "src/assets/steps";
import "src/styles/Register.scss";
import { CatchError } from "src/utils";
import { useAppDispatch, useAppSelector } from "src/hooks";
import { CheckUserConfig } from "src/server/config/Urls";
import { setApplied } from "src/redux/slices/application";
import Header from "src/components/home/Header";

function Register() {
  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const step = Number(location.pathname.split("/register/")[1]);
  const applied = useAppSelector((state) => state.Application.is_applied);

  const redirect = () => {
    if (
      location.pathname === "/register" ||
      location.pathname === "/register/"
    ) {
      navigate("/register/1");
    }
  };
  // Check user applied or not
  const UserAccess = async () => {
    if (!applied) {
      try {
        const { data } = await CheckUserConfig();
        dispatch(setApplied(data?.is_applied));
        if (data?.is_applied) {
          navigate("/register/4");
        }
      } catch (err) {
        CatchError(err);
      }
    }
  };

  useEffect(() => {
    redirect();
    UserAccess();
  }, []);

  return (
    <div className="register">
      <Header />

      {/* Body */}
      <div className="register__body">
        <div className="container">
          <h1 className="register__title">
            Malumotnomani toldirish va ariza topshirish
          </h1>

          {/* Apply progress bar */}
          <div className="register__nav">
            <div className="apply__progress flex">
              {/* Step 1 */}
              <span
                onClick={() => navigate("/register/1")}
                className={
                  step == 1
                    ? "progress-item"
                    : step < 1
                    ? "progress-item progress-unreach"
                    : "progress-item progress-done"
                }
              >
                <div className="progress-item__number">
                  {step > 1 ? <img src={success} /> : <span>1</span>}
                </div>
                <div className="progress-item__text">Shaxsiy ma'lumotlar</div>
                {step == 1 ? (
                  <img className="progress-item__img" src={step1} />
                ) : (
                  <img className="progress-item__img" src={step1done} />
                )}
              </span>

              {/* Step 2 */}
              <span
                onClick={() => navigate("/register/2")}
                className={
                  step == 2
                    ? "progress-item"
                    : step < 2
                    ? "progress-item progress-unreach"
                    : "progress-item progress-done"
                }
              >
                <div className="progress-item__number">
                  {step > 2 ? <img src={success} /> : <span>2</span>}
                </div>
                <div className="progress-item__text">Ta'lim malumotlari</div>
                {step < 2 && <img className="progress-item__img" src={step2} />}
                {step == 2 && (
                  <img className="progress-item__img" src={step2current} />
                )}
                {step > 2 && (
                  <img className="progress-item__img" src={step2done} />
                )}
              </span>

              {/* Step 3 */}
              <span
                onClick={() => navigate("/register/3")}
                className={
                  step == 3
                    ? "progress-item"
                    : step < 3
                    ? "progress-item progress-unreach"
                    : "progress-item progress-done"
                }
              >
                <div className="progress-item__number">
                  {step > 3 ? <img src={success} /> : <span>3</span>}
                </div>
                <div className="progress-item__text">Semmestr baxolari</div>
                {step < 3 && <img className="progress-item__img" src={step2} />}
                {step == 3 && (
                  <img className="progress-item__img" src={step2current} />
                )}
                {step > 3 && (
                  <img className="progress-item__img" src={step2done} />
                )}
              </span>

              {/* Step 4 */}
              <span
                className={
                  step == 4
                    ? "progress-item"
                    : step < 4
                    ? "progress-item progress-unreach"
                    : "progress-item progress-done"
                }
              >
                <div className="progress-item__number">
                  {step > 4 ? <img src={success} /> : <span>4</span>}
                </div>
                <div className="progress-item__text">Arizani topshirish</div>
                {step < 4 && <img className="progress-item__img" src={step3} />}
                {step == 4 && (
                  <img className="progress-item__img" src={step3current} />
                )}
                {step > 4 && (
                  <img className="progress-item__img" src={step3done} />
                )}
              </span>
            </div>
          </div>

          <div className="register__page">
            <Outlet />
          </div>
        </div>
      </div>

      {/* Footer */}
      <Footer />
    </div>
  );
}

export default Register;
