import {
  BrowserRouter as Router,
  Navigate,
  Route,
  Routes,
} from "react-router-dom";
import Home from "./pages/Home";
import NotFound from "./pages/NotFound";
import Profile from "./pages/Profile";
import Appelatsiya from "./pages/Profile/Appelatsiya";
import Applications from "./pages/Profile/Applications";
import Messages from "./pages/Profile/Messages";
import MyApplication from "./pages/Profile/MyApplication";
import Result from "./pages/Profile/Result";
import Register from "./pages/Register";
import Status from "./pages/Status";
import Step1 from "./pages/Steps/Step1";
import Step2 from "./pages/Steps/Step2";
import Step3 from "./pages/Steps/Step3";
import Step4 from "./pages/Steps/Step4";
import { token } from "./server/Host";
import "./styles/App.scss";

function App() {
  return (
    <>
      <Router>
        {/* All pages rendered here */}
        <Routes>
          <Route index element={<Home />} />
          <Route path="status" element={<Status />} />
          <Route
            path="profile"
            element={token ? <Profile /> : <Navigate to="/" />}
          >
            <Route path="results" element={<Result />} />
            <Route path="messages" element={<Messages />} />
            <Route path="appelatsiya" element={<Appelatsiya />} />
            <Route path="applications" element={<Applications />} />
          </Route>
          <Route
            path="register"
            element={token ? <Register /> : <Navigate to="/" />}
          >
            <Route path="1" element={<Step1 />} />
            <Route path="2" element={<Step2 />} />
            <Route path="3" element={<Step3 />} />
            <Route path="4" element={<Step4 />} />
          </Route>
          <Route
            path="my-application"
            element={token ? <MyApplication /> : <Navigate to="/" />}
          />
          <Route path="/*" element={<NotFound />} />
        </Routes>
      </Router>
    </>
  );
}

export default App;
