import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { prettyDate } from "src/utils";
import { EditSVG, RightArrowSVG } from "../svg";

function Application({ data, footer = true }: { data: any; footer: boolean }) {
  const navigate = useNavigate();
  const [status, setStatus] = useState("send");

  return (
    <div className="application">
      <div className="application__header flex">
        <h3 className="id">Ariza id: {data?.id}</h3>
        <div className="time">
          Ariza topshiorilgan vaqt: <b>{prettyDate(data?.created_at)}</b>
        </div>
      </div>
      <div className="application__body">
        {footer && (
          <div className="flex">
            <h5>Ariza holati:</h5>
            <div>
              {status === "send" && (
                <div className="label done">Ariza yuborildi</div>
              )}
              {status === "error" && (
                <div className="label error">Ariza rad etildi</div>
              )}
              {status === "done" && (
                <div className="label success">Ariza tasdiqlandi</div>
              )}
            </div>
          </div>
        )}

        <div className="flex">
          <h5>OTM nomi:</h5>
          <h2>{data?.university_name}</h2>
        </div>
        <div className="flex">
          <h5>Yonalish nomi:</h5>
          <h2>{data?.university_direction_name}</h2>
        </div>
        <div className="flex">
          <h5>Talim tili:</h5>
          <h2>{data?.education_language_name}</h2>
        </div>
        <div className="flex">
          <h5>Talim shakli:</h5>
          <h2>{data?.education_type_name}</h2>
        </div>
      </div>
      {footer && (
        <div className="application__footer flex">
          <button className="edit" onClick={() => navigate("/register/4")}>
            <EditSVG />
            <span>Tahrirlash</span>
          </button>
          <button
            className="arrowBtn"
            onClick={() => navigate("/my-application")}
          >
            <span>Batafsil</span>
            <RightArrowSVG />
          </button>
        </div>
      )}
    </div>
  );
}

export default Application;
