import { CreateFunc, GetFunc, EditFunc } from "./Requests";

// Login Configs ----------------------------------------------------------
export const LoginConfig = (data) => {
  return CreateFunc("/auth/token/", data);
};
export const GetAccessConfig = (data) => {
  return CreateFunc("/auth/token/refresh/", data);
};

// Regsiter Configs --------------------------------------------------------
export const CheckPhoneConfig = (phone) => {
  return GetFunc(`/auth/user/${phone}/`);
};
export const RegisterConfig = (data) => {
  return CreateFunc("/auth/registration/", data);
};
export const RegisterCheckConfig = (data) => {
  return CreateFunc("/auth/registration/check/", data);
};
export const ResendConfig = (data) => {
  return CreateFunc("/auth/reset/send-code/", data);
};

// Edit user password  -----------------------------------------------------
export const ChangePasswordConfig = (phone, data) => {
  return EditFunc(`/auth/reset/password/${phone}/`, data);
};
export const ChangePhoneConfig = (data) => {
  return EditFunc(`/users/change-phone-number/`, data);
};

// Get user info -----------------------------------------------------------
export const GetUserConfig = () => {
  return GetFunc(`/users/me/`);
};
export const CheckUserConfig = () => {
  return GetFunc(`/users/applied/`);
};
export const GetDiplomConfig = () => {
  return GetFunc(`/users/diploma/`);
};
export const GetApplicationConfig = () => {
  return GetFunc(`/users/my-application/`);
};
export const GetAppHistorynConfig = () => {
  return GetFunc(`/users/application-history/`);
};
export const GetUniverConfig = () => {
  return GetFunc(`/university/universities/`);
};
export const GetDirectionConfig = (id) => {
  return GetFunc(`/university/direction/${id}/`);
};

// Create application ---------------------------------------------------------
export const SubmitConfig = (data) => {
  return CreateFunc("/users/application/", data);
};
export const EditAppConfig = (data) => {
  return EditFunc("users/my-application/", data);
};
