import React, { useEffect, useState } from "react";
import { Button, Form, Select } from "antd";
import { RightArrowSVG } from "../../components/svg";
import { useNavigate } from "react-router-dom";
import {
  CheckUserConfig,
  EditAppConfig,
  GetApplicationConfig,
  GetDiplomConfig,
  GetDirectionConfig,
  GetUniverConfig,
  SubmitConfig,
} from "src/server/config/Urls";
import { CatchError } from "src/utils";
import { university, language } from "src/types";
import { useAppDispatch, useAppSelector } from "src/hooks";
import { otm, yonalish, lang, turi } from "src/assets/rules";
import { setApplication } from "src/redux/slices/application";
import { token } from "src/server/Host";

function Step4() {
  const { Option } = Select;
  const [form] = Form.useForm();
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const [types, setTypes] = useState<any>([]);
  const [languages, setLanguages] = useState<any>([]);
  const [directions, setDirections] = useState<any>([]);
  const [universities, setUniversities] = useState<any>([]);
  const isApplied = useAppSelector((state) => state.Application.is_applied);
  const application = useAppSelector((state) => state.Application.application);

  const GetDiplom = async () => {
    try {
      const { data } = await GetDiplomConfig();
      if (!data?.can_apply) {
        navigate("/status");
      }
    } catch (error) {
      CatchError(error);
    }
  };
  const getUniversity = async () => {
    try {
      const { data } = await GetUniverConfig();
      setUniversities(data);
    } catch (err) {
      CatchError(err);
    }
  };
  const getApplication = async () => {
    // Set Application info
    if (Object.keys(application).length < 1) {
      const { data } = await GetApplicationConfig();
      dispatch(setApplication(data));
      fillFields(data);
    } else {
      fillFields(application);
    }
  };
  const UserAccess = async () => {
    if (token) {
      try {
        const { data } = await CheckUserConfig();
        if (data?.is_applied) {
          getApplication();
        }
      } catch (err) {
        CatchError(err);
      }
    }
  };

  // Handlers for select
  const handleUniveristy = (univerID: number) => {
    if (univerID) {
      setDirections(
        universities.find((univer: university) => univer.id == univerID)[
          "directions"
        ]
      );
    } else {
      setDirections([]);
    }
    setLanguages([]);
    setTypes([]);
    form.setFieldsValue({
      university_direction: null,
      education_language: null,
      education_type: null,
    });
  };
  const handleDirection = async (directionID: number) => {
    if (directionID) {
      const { data } = await GetDirectionConfig(directionID);
      setLanguages(data);
    } else {
      setLanguages([]);
    }
    setTypes([]);
    form.setFieldsValue({
      education_language: null,
      education_type: null,
    });
  };
  const handleLanguage = (langID: number) => {
    if (langID) {
      setTypes(languages.find((lang: language) => lang.id == langID)["types"]);
    } else {
      setTypes([]);
    }
    form.setFieldsValue({
      education_type: null,
    });
  };
  const fillFields = (data: any) => {
    form.setFieldsValue({
      university: {
        label: data?.university_name,
        value: data?.university,
      },
      university_direction: {
        label: data?.university_direction_name,
        value: data?.university_direction,
      },
      education_language: {
        label: data?.education_language_name,
        value: data?.education_language,
      },
      education_type: {
        label: data?.education_type_name,
        value: data?.education_type,
      },
    });

    handleLanguage(data?.education_language);
  };

  // Submit data
  const submit = async (val: any) => {
    try {
      if (!isApplied) {
        await SubmitConfig(val);
        navigate("/profile/applications");
      } else {
        if (Object.keys(val.education_language).length < 1) {
          await EditAppConfig(val);
        } else {
          await EditAppConfig({
            education_language: val.education_language.value,
            education_type: val.education_type.value,
            university: val.university.value,
            university_direction: val.university_direction.value,
          });
        }
        navigate("/profile/applications");
      }
    } catch (err) {
      CatchError(err);
    }
  };

  useEffect(() => {
    GetDiplom();
    UserAccess();
    getUniversity();
  }, []);
  return (
    <>
      <div className="step4">
        <div className="title">Arizani topshirish</div>
        <Form form={form} className="form" layout="vertical" onFinish={submit}>
          {/* OTM nomi* yonalish */}
          <div className="form__row flex">
            {/* OTM nomi* */}
            <Form.Item name="university" label="OTM nomi" rules={otm}>
              <Select
                placeholder="OTM nomini tanlang"
                size={"large"}
                onChange={handleUniveristy}
                allowClear
              >
                {universities.map((univer: university, index: number) => (
                  <Option value={univer?.id} key={index}>
                    {univer.name}
                  </Option>
                ))}
              </Select>
            </Form.Item>

            {/* Yo'nalish */}
            <Form.Item
              name="university_direction"
              label="Yonalish"
              rules={yonalish}
            >
              <Select
                placeholder="Yonalishni tanlang"
                size={"large"}
                onChange={handleDirection}
                allowClear
              >
                {directions.map((univer: university, index: number) => (
                  <Option value={univer?.id} key={index}>
                    {univer.name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </div>

          {/* Talim tili va yonalishi */}
          <div className="form__row flex">
            {/* Talim tili* */}
            <Form.Item
              name="education_language"
              label="Talim tili"
              rules={lang}
            >
              <Select
                placeholder="Talim tilini tanlang"
                size={"large"}
                onChange={handleLanguage}
                allowClear
              >
                {languages.map((lang: language, index: number) => (
                  <Option value={lang.id} key={index}>
                    {lang.name}
                  </Option>
                ))}
              </Select>
            </Form.Item>

            {/* Talim shakli* */}
            <Form.Item name="education_type" label="Talim shakli" rules={turi}>
              <Select
                placeholder="Talim shaklini tanlang"
                size={"large"}
                allowClear
              >
                {types.map(
                  (type: { id: number; name: string }, index: number) => (
                    <Option value={type.id} key={index}>
                      {type.name}
                    </Option>
                  )
                )}
              </Select>
            </Form.Item>
          </div>

          <div className="step__bottom">
            <button className="back" onClick={() => navigate("/register/3")}>
              <span>Orqaga</span>
            </button>
            <Form.Item>
              <Button htmlType="submit" className="arrowBtn">
                <span>Keyingi qadam</span>
                <RightArrowSVG />
              </Button>
            </Form.Item>
          </div>
        </Form>
      </div>
    </>
  );
}

export default Step4;
