import React from "react";
import { Alert } from "antd";

function Messages() {
  return (
    <div className="profile__messages">
      <h2 className="title">Xabarlar</h2>
      <div className="messages">
        <Alert
          message="Diqqat"
          description="Hurmatli bitiruvchila arizangiz muvaffaqiyatli yuborildi. Arizani 2022-yilning 5-iyulidan 25-iyuliga qadar tahrirlash mumkin."
          type="success"
          showIcon
        />
      </div>
    </div>
  );
}

export default Messages;
