import { Button, Form, Image, Input } from "antd";
import { useEffect, useState } from "react";
import { RightArrowSVG } from "../../components/svg";
import { ChangePhoneConfig, GetUserConfig } from "../../server/config/Urls";
import { phone } from "../../assets/rules";
import { token } from "../../server/Host";
import { useNavigate } from "react-router-dom";
import { CatchError } from "../../utils";

function Step1() {
  const navigate = useNavigate();
  const [user, setUser] = useState<any>({});
  const [form] = Form.useForm();

  // Get user from API
  const GetUser = async () => {
    if (token) {
      try {
        const { data } = await GetUserConfig();
        setUser(data);
        // Fill default values
        form.setFieldsValue({
          citizenship: data.citizenship?.name,
          nationality: data.nationality?.name,
          pasport: `${data.passport_serial} ${data.passport_number}`,
          pinfl: data?.pinfl,
          name: data?.first_name,
          lastname: data?.last_name,
          otasi: data?.middle_name,
          birthday: data?.birth_date,
          viloyat: data.permanent_district?.region?.name,
          tuman: data.permanent_district?.name,
          address: data?.permanent_address,
          jinsi: data?.gender === "1" ? "Erkak" : "Ayol",
          phone: data?.phone_number.slice(3),
        });
      } catch (err) {
        CatchError(err);
      }
    } else {
      window.location.href = "/status";
    }
  };

  // Finish step 1
  const FinishStep1 = async (val: any) => {
    try {
      await ChangePhoneConfig({
        phone_number: `998${val.phone}`,
      });
      navigate("/register/2");
      window.scrollTo(0, 0);
    } catch (err) {
      CatchError(err);
    }
  };

  useEffect(() => {
    GetUser();
  }, []);

  return (
    <>
      <div className="step1">
        <div className="title">Shaxsiy ma'lumotlar</div>
        <Form
          form={form}
          className="form"
          layout="vertical"
          onFinish={FinishStep1}
        >
          {/* Fuqorlik va MIllati */}
          <div className="form__row flex">
            {/* Fuqorolik */}
            <Form.Item name="citizenship" label="Fuqaroligi">
              <Input placeholder="Ozbekiston" disabled />
            </Form.Item>

            {/* MIllati */}
            <Form.Item name="nationality" label="Millati">
              <Input placeholder="Ozbek " disabled />
            </Form.Item>
          </div>

          {/* Passport and jsshr */}
          <div className="form__row flex">
            {/* Pasport */}
            <Form.Item name="pasport" label="Pasport seriya raqami">
              <Input placeholder="AA0000000" disabled />
            </Form.Item>

            {/* JSHSHIR */}
            <Form.Item name="pinfl" label="JSHSHIR">
              <Input placeholder="1234567891011121314" disabled />
            </Form.Item>
          </div>

          {/* Rasm ismi boshqalar */}
          <div className="form__row flex">
            {/* Image */}
            <div className="form__image">
              <Image
                width={160}
                src={user?.photo && `data:image/jpeg;base64,${user?.photo}`}
              />
            </div>

            <div className="">
              <div className="flex24">
                <Form.Item name="name" label="Ismi">
                  <Input placeholder="Abror" disabled />
                </Form.Item>

                <Form.Item name="lastname" label="Familiyasi">
                  <Input placeholder="Askarov" disabled />
                </Form.Item>
              </div>
              <div className="flex24">
                <Form.Item name="otasi" label="Otasini Ismi">
                  <Input placeholder="Akmal ogli" disabled />
                </Form.Item>
                <Form.Item name="birthday" label="Tugilgan sanasi">
                  <Input placeholder="25-08-2001" disabled />
                </Form.Item>
              </div>
            </div>
          </div>

          {/* Tugilgan viloyat* and tuman */}
          <div className="form__row flex">
            <Form.Item name="viloyat" label="Tugilgan viloyat">
              <Input placeholder="Buxoro viloyati" disabled />
            </Form.Item>

            <Form.Item name="tuman" label="Tugilgan tumani*">
              <Input placeholder="Vobkent tumani" disabled />
            </Form.Item>
          </div>

          {/* Address */}
          <div className="form__row flex">
            <Form.Item name="address" label="Tugilgan mazili">
              <Input placeholder="Pirmast qfy" disabled />
            </Form.Item>
          </div>

          {/* Jinsi va millati */}
          <div className="form__row flex">
            <Form.Item name="jinsi" label="Jinsni tanlash*">
              <Input placeholder="Erkak" disabled />
            </Form.Item>

            <Form.Item name="phone" label="Telefon raqam" rules={phone}>
              <Input placeholder="00 000 00 00" addonBefore="+998" />
            </Form.Item>
          </div>

          <div className="step__bottom">
            <Form.Item>
              <Button htmlType="submit" className="arrowBtn">
                <span>Keyingi qadam</span>
                <RightArrowSVG />
              </Button>
            </Form.Item>
          </div>
        </Form>
      </div>
    </>
  );
}

export default Step1;
